import axios from 'axios';
import Gzip from './gzip';
import ViewModel from '../shell/viewmodel/viewmodel';
import Cipher from './cipher';

let { getGlobalStore, setGlobalStore } = ViewModel;
let gziptools = new Gzip();

export default function changeUrl({ url, busiaction, userId, gzipFlag, cipherFlag, aeskey, pagecs, userCode }, callBack) {
    let confirmShuntApi = '/nccloud/platform/pub/clientdispatch.do';
    let ShuntServerInfo = getGlobalStore('ShuntServerInfo');
    let newUrl = url;

    //: workbenchajax

    //  
    let dataParams = {
        sysParamJson: {
            pagecs,
            busiaction: '',
            ts: new Date().getTime()
        }
    };

    let header = { 'Content-Type': 'application/json;charset=UTF-8' };
    dataParams = addDataSign(dataParams, '', { allin: true, headers: header });


    // 
    if (ShuntServerInfo === null && userCode && userId) {
        axios({
            method: 'post',
            url: confirmShuntApi,
            headers: header,
            data: dataParams,
            transformRequest: [
                function (data) {
                    let gData = gzipFlag ? gziptools.zip(JSON.stringify(data)) : data;
                    return cipherFlag ? Cipher.encrypt(gData, aeskey) : gData;
                }
            ],
            transformResponse: [
                function (data) {
                    //let resData = gzipFlag ? gziptools.unzip(data) : data;
                    //return resData;
                    data = typeof data === 'string' && cipherFlag ? Cipher.decrypt(data, aeskey) : data;
                    return gzipFlag ? gziptools.unzip(data) : isJSON(data) ? JSON.parse(data) : data;
                }
            ],
            withCredentials: true
        }).then((res) => {
            if (res.data.success && res.data.data && res.data.data.rule) {
                let rule = res.data.data.rule;
                if (rule) {
                    let userRule = res.data.data.userRule;
                    if (userRule) {
                        rule = Object.assign(rule, userRule);
                    }
                    setGlobalStore('ShuntServerInfo', rule);
                    newUrl = getNewUrl(rule, url, busiaction, userId);
                    callBack(newUrl);
                } else {
                    // 
                    callBack(newUrl);
                }
            } else {
                // 
                callBack(newUrl);
            }
        }).catch(() => {
            // 
            callBack(newUrl);
        });

    } else {
        newUrl = getNewUrl(ShuntServerInfo, url, busiaction, userId);
        callBack(newUrl);
    }
}


//NCCloudURI
function getNewUrl(rule, url, busiaction, userId) {
    let urlArr = url.split('/');
    if (urlArr.length >= 2 && urlArr[1] === 'nccloud' && rule) {
        // + url 
        let key1 = busiaction + '###' + url.split('/').join('.').slice(1);
        if (rule[key1]) {
            urlArr.splice(2, 0, rule[key1]);
            return urlArr.join('/');
        }

        //
        let key2 = busiaction + '###';
        if (rule[key2]) {
            urlArr.splice(2, 0, rule[key2]);
            return urlArr.join('/');
        }

        // url 
        let key3 = '###' + url.split('/').join('.').slice(1);
        if (rule[key3]) {
            urlArr.splice(2, 0, rule[key3]);
            return urlArr.join('/');
        }

        //
        let key4 = userId;
        if (rule[key4]) {
            urlArr.splice(2, 0, rule[key4]);
            return urlArr.join('/');
        }
    }

    return url;
}


// URL ip/port
//  port  Access-Control-Allow-Origin   
//  ip cookieip  
// URL
//  -- by bbqin
function addDataSign(data, userCode, {
	allin = false,
	headers = {}
}) {
	// console.log(data, userCode);
	if (
		typeof transSn === 'undefined' ||
		typeof transSign === 'undefined' ||
		// !userCode ||
		(headers && headers['Content-Type'] == 'multipart/form-data')  // newData.append(    
	) {
		return data;
	}
	//  

	//  
	if (!allin) {
		let strDataSign = data.busiParamJson + '';
		//  -- bbqin
		data.sysParamJson.sn = transSn(userCode);
		data.sysParamJson.signdata = transSign(strDataSign, userCode);
	} else {
		//  -- bbqin
		// data.sysParamJson.sn = transSn(userCode);
		let dataStr = JSON.stringify(data) + '';
		headers['Identifications'] = transSign(dataStr, '');
	}
	//   headers   TODO 
	return data;
}


function isJSON(str) {
    if (typeof str == 'string') {
        try {
            var obj = JSON.parse(str);
            if (typeof obj == 'object' && obj) {
                return true;
            } else {
                return false;
            }
        } catch (e) {
            console.log('error' + str + '!!!' + e);
            return false;
        }
    }
    console.log('It is not a string!');
}




