export function getLangData(locale) {
	//  
	//return langData.vietnamese;
	switch (locale) {
		case 'simpchn':
			return langData.simpchn;
			break;
		case 'english':
			return langData.english;
			break;
		case 'tradchn':
			return langData.tradchn;
			break;
		case 'vietnamese':
			return langData.vietnamese;
			break;
		default:
			return langData.simpchn;
	}
}

export let locale = getLangData(localeLang);

// 
export function DongbaToLocalTime(timer) {
	if (!timer) {
		return '';
	}
	//8
	let zoneOffset = 8;
	//,
	let offset2 = timer.toDate().getTime();
	// 0
	let GMTDate = offset2 - zoneOffset * 60 * 60 * 1000;
	//0
	let newDateOffset = moment().toDate().getTimezoneOffset() * 60 * 1000;
	let localTime = new Date(GMTDate - newDateOffset);
	return moment(localTime);
}
export function isDongba() {
	return new Date().getTimezoneOffset() / 60 === -8;
}
// 
export function LocalToDongbaTime(timer) {
	if (!timer) {
		return '';
	}
	//8
	let zoneOffset = 8;
	//,
	let offset2 = moment().toDate().getTimezoneOffset() * 60 * 1000;
	//
	let nowDate2 = timer.toDate().getTime();
	return moment(new Date(nowDate2 + offset2 + zoneOffset * 60 * 60 * 1000));
}
/* functionValue flag  */
class timeFunctionTranslaterBase {
	constructor() {
		Date.prototype.toLocaleDateString = function () {
			return moment(this).format('YYYY/MM/DD');
		};
	}

	//
	translater = (functionValue, config) => {
		let result = functionValue;
		if (typeof functionValue === 'string' && this.timeFunctionMap.hasOwnProperty(functionValue)) {
			let date = this.timeFunctionMap[functionValue](config);
			result = this.multiTimeZone(date, config);
		}
		// console.log('',result)
		return result;
	};

	//
	timeFunctionMap = {
		'#day(-1)#': config => this.getDay(-1, config), //  
		'#day(0)#': config => this.getDay(0, config), //  
		'#day(1)#': config => this.getDay(1, config), //  
		'#month(-1)#': config => this.getMonth(-1, config), // 
		'#month(0)#': config => this.getMonth(0, config), // 
		'#month(1)#': config => this.getMonth(1, config), // 
		'#week(-1)#': config => this.getWeek(-1, config), //
		'#week(0)#': config => this.getWeek(0, config), //
		'#week(1)#': config => this.getWeek(1, config), //
		'#quarter(-1)#': config => this.getQuarter(-1, config), //  
		'#quarter(0)#': config => this.getQuarter(0, config), //
		'#quarter(1)#': config => this.getQuarter(1, config), //  
		'#year(-1)#': config => this.getYear(-1, config), //
		'#year(0)#': config => this.getYear(0, config), // 
		'#year(1)#': config => this.getYear(1, config), //
		'#lastDayOfMonth#': config => this.getMonthLastDay(0, config), //  
		'#lastDayOfWeek#': config => this.getWeekLastDay(0, config), // 
		'#finalDayOfLastMonth#': config => this.getMonthLastDay(-1, config) // 
	};

	multiTimeZone = (data, config) => {
		let { format, isMultiTimeZone } = config;
		if (isMultiTimeZone) {
			return LocalToDongbaTime(moment(data)).format(format);
		} else {
			return moment(data).format(format);
		}
	};

	getDay = (value, config) => {
		let { flag, format, isMultiTimeZone } = config;
		let oneDay = 24 * 60 * 60 * 1000;
		return new Date(
			new Date(new Date().toLocaleDateString()).getTime() + oneDay * value + (!flag ? oneDay - 1 : 0)
		);
	};

	getMonth = (value, config) => {
		let { flag, format, isMultiTimeZone } = config;
		var nowdays = new Date();
		var year = nowdays.getFullYear();
		var month = nowdays.getMonth() + value + (flag ? 0 : 1);
		if (month < 0) {
			month = 11;
			year = year - 1;
		}
		if (month < 10) {
			month = '0' + month;
		}
		return new Date(new Date(new Date(year, month, '01').toLocaleDateString()).getTime() - (flag ? 0 : 1));
	};

	getWeek = (value, config) => {
		let { flag, format, isMultiTimeZone } = config;
		var date = new Date();
		let weekday = date.getDay() || 7;
		let oneWeek = 7 * 24 * 60 * 60 * 1000;
		date.setDate(date.getDate() - weekday + 1);
		return new Date(new Date(date.toLocaleDateString()).getTime() + oneWeek * value + (flag ? 0 : oneWeek - 1));
	};

	getQuarter = (value, config) => {
		let { flag, format, isMultiTimeZone } = config;
		var date = new Date();
		var month = date.getMonth();
		var year = date.getFullYear();

		let d = 0;
		if (month < 3) {
			d = 0 + (value + (flag ? 0 : 1)) * 3;
			date.setMonth(d);
		} else if (month > 2 && month < 6) {
			d = 3 + (value + (flag ? 0 : 1)) * 3;
			date.setMonth(d);
		} else if (month > 5 && month < 9) {
			d = 6 + (value + (flag ? 0 : 1)) * 3;
			date.setMonth(d);
		} else if (month > 8 && month <= 11) {
			d = 9 + (value + (flag ? 0 : 1)) * 3;
			date.setMonth(d);
		}
		if (d < 0) {
			year--;
		} else if (d > 11) {
			year++;
		}
		date.setDate(1);
		date.setFullYear(year);
		return new Date(new Date(date.toLocaleDateString()).getTime() - (flag ? 0 : 1));
	};

	getYear(value, config) {
		let { flag, format, isMultiTimeZone } = config;
		var date = new Date();
		var year = date.getFullYear() + value + (flag ? 0 : 1);
		date.setFullYear(year);
		date.setMonth(0);
		date.setDate(1);
		return new Date(new Date(date.toLocaleDateString()).getTime() - (flag ? 0 : 1));
	}

	getWeekLastDay(value, config) {
		let { flag } = config;
		var date = new Date();
		let weekday = date.getDay() || 7;
		let oneDay = 24 * 60 * 60 * 1000;
		let oneWeek = 7 * 24 * 60 * 60 * 1000;
		date.setDate(date.getDate() - weekday);
		return new Date(
			new Date(date.toLocaleDateString()).getTime() + oneWeek * (value + 1) + (flag ? 0 : oneDay - 1)
		);
	}

	getMonthLastDay(value, config) {
		let { flag } = config;
		var date = new Date();
		let oneDay = 24 * 60 * 60 * 1000;
		var year = date.getFullYear();
		var month = date.getMonth() + value + 1;
		if (month >= 12) {
			month = month - 12;
			year = year + 1;
		}
		if (month < 10) {
			month = '0' + month;
		}
		date.setFullYear(year);
		date.setMonth(month);
		date.setDate(1);
		return new Date(new Date(date.toLocaleDateString()).getTime() - (flag ? oneDay : 1));
	}
}

export function timeFunctionTranslater(functionValue, config) {
	config = Object.assign({ flag: true, format: 'YYYY-MM-DD HH:mm:ss', isMultiTimeZone: true }, config);
	let regu = /^#.*#$/;
	let re = new RegExp(regu);
	if (re.test(functionValue)) {
		let timeFunctionTranslater = new timeFunctionTranslaterBase();
		return timeFunctionTranslater.translater(functionValue, config);
	} else {
		// if(config.isMultiTimeZone){
		//    return LocalToDongbaTime(moment(functionValue)).format(config.format);
		// }else{
		//     return functionValue;
		// }
		return functionValue;
	}
}

export function getClientHeight() {
	var scrollTop = 0;
	if (document.documentElement && document.documentElement.scrollTop) {
		scrollTop = document.documentElement.clientHeight;
	} else if (document.body) {
		scrollTop = document.body.clientHeight;
	}
	return scrollTop;
}

export function getClientWidth() {
	var scrollTop = 0;
	if (document.documentElement && document.documentElement.scrollTop) {
		scrollTop = document.documentElement.clientWidth;
	} else if (document.body) {
		scrollTop = document.body.clientWidth;
	}
	return scrollTop;
}

// NCC   moment 
export function langCodeToMoment(langCode) {
	//  moment   
	//langCode = 'vietnamese'
	switch (langCode) {
		case 'simpchn':
			moment.locale('zh-cn');
			break;
		case 'english':
			moment.locale('en');
			break;
		case 'tradchn':
			moment.locale('zh-tw');
			break;
		case 'vietnamese':
			// moment.locale('vi', {
			// 	months: 'Thng mt_Thng hai_Thng ba_Thng bn_Thng nm_Thng su_Thng by_Thng tm_Thng chn_Thng mi_Thng mi mt_Thng mi hai'.split('_'),
			// 	monthsShort: 'Thng 1_Thng 2_Thng 3_Thng 4_Thng 5_Thng 6_Thng 7_Thng 8_Thng 9_Thng 10_Thng 11_Thng 12'.split('_'),
			// 	weekdays: 'Ch nht_Th hai_Th ba_Th t_Th nm_Th su_Th by'.split('_'),
			// 	weekdaysShort: 'Ch nht_Th hai_Th ba_Th t_Th nm_Th su_th by'.split('_'),
			// 	weekdaysMin: 'Ch nht_Th hai_Th ba_Th t_Th nm_Th su_Th by'.split('_')
			// });
			moment.locale('vi')
			break;
		default:
			moment.locale('zh-cn');
	}
}

