export function debounds(callback ,delay=0){
    let timeout = null;
    return function(){
        let that = this;
        let param = arguments;
        if(time){
            clearTimeout(time);
        }
       timeout = setTimeout(function(){
            typeof callback==='function'&&callback.apply(that,param);
       },delay);
    }
}
/*
 * @method   
 * @author   add by xuyangt @19/03/08
 * @params
 *     callback     {fun}      
 *     delay     {number}   
 * @return   {fun}
 * @demo
 */
export function throttle(callback ,interval=0){
    let last = 0;
    return function(){
        let param = arguments;
        let that = this;
        let now = +new Date();
        if(now - last>= interval){
            last = now;
            typeof callback==='function'&&callback.apply(that,param);
        }
    }
}

